<template>
    <Transition name="modal">
        <div class="modal" @click="closeModal">
            <form class="modal__block" @click.stop="">
                <h2 class="modal__title">{{ edit ? $t('changeNote') : $t('addNote') }}</h2>
                <div class="modal__inputs">
                    <label>
                        <span>Title</span>
                        <input type="text" :placeholder="$t('inputTitle')" v-model="title" :required="requiredCondition">=
                    </label>
                    <label>
                        <span>Content</span>
                        <textarea :placeholder="$t('txtarCont')" v-model="desc" :required="requiredCondition"></textarea>
                    </label>
                </div>
                <div class="modal__btns">
                    <button class="modal__btn cancel" @click="closeModal">{{ $t('cancel') }}</button>
                    <button class="modal__btn add" @click="addNote" v-if="edit == false">{{ $t('addBtn') }}</button>
                    <button class="modal__btn change" @click="changeNote" v-else>{{ $t('changeBtn') }}</button>
                </div>
            </form>
        </div>
    </Transition>
</template>

<script>
import { v4 as uuidv4 } from "uuid";

export default {
    data() {
        return {
            title: '',
            desc: '',
            date: new Date().toISOString().slice(0, 10).split('-').reverse().join('.'),

            requiredCondition: true,
        }
    },
    methods: {
        closeModal() {
            this.$emit('closeModal', false);
            this.title = '';
            this.desc = '';
        },
        addNote() {
            if (this.title != '' && this.desc != '') {
                let item = {
                    id: uuidv4(), title: this.title, desc: this.desc, date: this.date
                };
                this.$emit('addNote', item);
                this.title = '';
                this.desc = '';
            }
        },
        changeNote() {
            this.requiredCondition = true;
            if (this.title != '' && this.desc != '') {
                const editedNote = {
                    id: this.editNote.id,
                    title: this.title,
                    desc: this.desc,
                    date: this.date,
                };
                this.$emit('editedNote', editedNote);
                this.closeModal();
            }
        }

    },
    props: {
        edit: Boolean,
        editNote: Object,
    },
};
</script>

<style>
.modal {
    background: rgba(0, 0, 0, 0.35);
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 999;
    display: flex;
    justify-content: center;
    align-items: center;
}

.modal__block {
    background: linear-gradient(0deg, rgba(103, 80, 164, 0.11), rgba(103, 80, 164, 0.11)), #FFFBFE;
    border-radius: 28px;
    max-width: 312px;
    width: 100%;
    padding: 24px;
}

.modal__title {
    font-weight: 400;
    font-size: 24px;
    line-height: 32px;
    color: #1C1B1F;
    margin-bottom: 16px;
}

.modal__inputs {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    margin-bottom: 24px;
}

.modal__inputs label {
    position: relative;
}

.modal__inputs span {
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.4px;
    color: #6750A4;
    position: absolute;
    top: 8px;
    left: 16px;
}

.modal__inputs input,
.modal__inputs textarea {
    background: #E7E0EC;
    border-radius: 4px 4px 0px 0px;
    padding: 23px 16px 9px;
    width: 100%;
    font-weight: 400;
    font-size: 16px;
    letter-spacing: 0.5px;
    color: #49454F;
    border-bottom: 1px solid #1C1B1F;
}

.modal__inputs textarea {
    padding: 23px 16px 0;
}

.modal__inputs input::placeholder,
.modal__inputs textarea::placeholder {
    font-weight: 400;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: 0.5px;
    color: #49454F;
}

.modal__btns {
    display: flex;
    justify-content: flex-end;
    column-gap: 8px;
}

.modal__btn {
    border-radius: 5px;
    padding: 10px 12px;
    font-weight: 500;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    letter-spacing: 0.1px;
    text-transform: uppercase;
    transition: 0.5s;
}

.cancel {
    color: #CF1B1B;
}

.cancel:hover {
    background: #FFE1E1;
}

.add,
.change {
    color: #6750A4;
}

.add:hover,
.change:hover {
    background: #E6DDFF;
}

.modal-enter-active,
.modal-leave-active {
    transition: all 0.2s linear;
}

.modal-enter-from,
.modal-leave-to {
    opacity: 0;
    transform: scale(1.3);
}
</style>