<template>
  <a class="tools__cont_before" tabindex="0"></a>
  <div class="tools__cont">
    <button @click="check()" class="checkBtn">{{ varMtCheckBtn }}</button>
    <button @click="clearConsole()" class="checkBtn">{{ checkBtn }}</button>
  </div>
</template>

<script>
export default {
  data() {
    return {
      varMtCheckBtn: "*var||Method Checker",
      checkBtn: "*toClearConsole",
    };
  },
  computed: {},
  methods: {
    check() {
      var name = prompt(
        "Введите название переменного или метода в коде Vue, которого вы хотите проверить:"
      );
      var checkMethod = confirm(
        "Каким способом проверять:\n1) Проверить на существования переменного или метода? (OK-true)\n2) Проверить метод (только метод) с посредством его запуска? (Cancel-false)"
      );
      console.clear();
      this.checker(name, checkMethod);
    },
    checker(VarMethodName, WayToCheck) {

      if (WayToCheck) {
        if (
          typeof this[VarMethodName] == "undefined" &&
          typeof this.$parent[VarMethodName] == "undefined"
        ) {
          console.error("Метод или переменное с таким названием не найден :(");
          console.log(this[VarMethodName]);
        } else {
          console.log(
            `Поиск завершен успешно, найдены данные - ${VarMethodName}:`
          );
          if (`${this[VarMethodName]}` || `${this.$parent[VarMethodName]}` == '[object Object]') {
            let toolsObject = (`${JSON.stringify(this[VarMethodName])}` == 'undefined') ? 'нет' : JSON.stringify(this[VarMethodName]);
            let parentObject = (`${JSON.stringify(this.$parent[VarMethodName])}` == 'undefined') ? 'нет' : JSON.stringify(this.$parent[VarMethodName]);
            console.log(
              "В компоненте tools: " +
              toolsObject +
              "\nВ родительском компоненте: " +
              parentObject
            );
          } else {
            console.log(
              "В компоненте tools: " +
              this[VarMethodName] +
              "\nВ родительском компоненте: " +
              this.$parent[VarMethodName]
            );
          }
        }
      } else {
        if (
          typeof this[VarMethodName] == "function" ||
          typeof this.$parent[VarMethodName] == "function"
        ) {
          console.warn("Успешно найден, запуск метода...");
          try {
            this[VarMethodName]();
          } catch (error) {
            console.log("Оно находится в родительском компоненте:");
            this.$parent[VarMethodName]();
          }
        } else {
          console.error("Метод с таким названием не найден :(");
        }
      }
    },
    clearConsole() {
      console.clear();
    },
  },
};
</script>

<style scoped>
.tools__cont {
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  position: absolute;
  top: 0;
  right: 0;
  transition: 500ms;
  transform: scale(0);
  z-index: 9999;
}

.tools__cont_before {
  position: absolute;
  top: 0;
  right: 0;
  width: 15px;
  height: 15px;
  background: red;
  z-index: 9999;
}

.tools__cont_before:focus {
  transform: scale(0);
}

.tools__cont_before:focus+.tools__cont {
  transform: scale(1);
}

.checkBtn {
  width: 120px;
  border: 2px solid red;
  border-radius: 5%;
  background: #000;
  color: red;
  padding: 5px;
  transition: 300ms;
}

.checkBtn:focus {
  background: red;
  color: black;
}
</style>