<template>
  <header class="header">
    <Transition name="header__notes">
      <div class="header__notes" v-show="header === true">
        <button @click="changeLang" class="header__lang">{{ $i18n.locale }}</button>
        <h1 class="header__title">{{ $t('navTitle') }}</h1>
        <button class="header__search" @click="header = !header">
          <img src="@/assets/img/search.svg" alt="" />
        </button>
      </div>
    </Transition>
    <Transition name="header__form">
      <div class="header__form" v-show="header === false">
        <button class="header__back" @click="(header = !header), (search = '')">
          <img src="@/assets/img/back.svg" alt="" />
        </button>
        <div class="container">
          <input type="text" class="header__input" :placeholder="$t('placeholder')" v-model="search" />
        </div>
        <button class="header__close" @click="search = ''">
          <img src="@/assets/img/close.svg" alt="" />
        </button>
      </div>
    </Transition>
  </header>
</template>

<script>
export default {
  data() {
    return {
      header: true,
      search: "",
    };
  },
  watch: {
    search(val) {
      this.$emit("searchValue", val);
    },
  },
  methods: {
    changeLang() {
      this.$i18n.locale == 'ru' ? this.$i18n.locale = 'uz' : this.$i18n.locale = 'ru';
      localStorage.lang = this.$i18n.locale;
    }
  },
};
</script>

<style scoped>
.header {
  background: #f3edf7;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25), 0px 1px 3px rgba(0, 0, 0, 0.3);
  padding: 18px 22px;
  height: 70px;
  overflow: hidden;
}

.header__notes,
.header__form {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.header__lang {
  font-size: 22px;
  font-weight: 700;
  line-height: 28px;
  text-transform: uppercase;
}

.header__title {
  font-weight: 400;
  font-size: 22px;
  line-height: 28px;
  color: #1c1b1f;
}

.header__input {
  display: block;
  width: 100%;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  color: #9d9d9d;
}

.header__notes-enter-active,
.header__notes-leave-active {
  transition: 500ms;
}

.header__notes-enter-from,
.header__notes-leave-to {
  opacity: 0;
  transform: translateY(-200px);
}

.header__notes-enter-to,
.header__notes-leave-from {
  opacity: 1;
  transform: translateY(0);
}

.header__form-enter-active,
.header__form-leave-active {
  transition: 500ms linear;
}

.header__form-enter-from,
.header__form-leave-to {
  opacity: 0;
  transform: translateY(200px);
}

.header__form-enter-to,
.header__form-leave-from {
  opacity: 1;
  transform: translateY(0);
}
</style>